import design from '../../components/Design.vue'
export default {
	data() {
		return {
			myid:-1,
			detail:{},
			background1:"#f2f2f2",
			background2:"#fff"
		}
	},
	methods: {
		getdetails() {
			uni.request({
				url:"https://web58.top/zt/json/nhqc/vehicles.json",
				success:(res) => {
					this.detail=res.data.vehicles[this.myid-1]
					//console.log(this.detail)
					uni.setNavigationBarTitle({
						title:this.detail.header,
					
					});	
				}
			})
		},
		yuyue() {
			uni.showToast({
				title: '暂不支持',
				duration: 2000,
				icon: "none",
				position:"bottom"					
			})
		}
	},
	components:{
		design
	},
	onLoad(options) {
		this.myid=options.id;
		this.getdetails();
	
	}
}
